.house_item {
  border-bottom: 1px solid #ccc;
  display: flex;
  padding: 10px;
  .house_img {
    width: 33%;
    display: flex;
    align-items: center;
    img {
    }
  }

  .house_info {
    flex: 1;
    overflow: hidden;
    padding-left: 10px;
    >div{
      padding-top: 5px;
      padding-bottom: 5px;
    }
    .house_title {
      // margin-bottom: 5px;
      color: #000;
      font-weight: 600;
      font-size: 15px;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }

    .house_desc {
      font-size: 13px;
      // margin-bottom: 5px;
      color: #999;
    }

    .house_tags {
      font-size: 12px;
      display: flex;
      overflow: hidden;
      width: 100%;
      .house_tags_item {
        margin-right: 5px;
        // margin-bottom: 5px;
        padding: 2px;
        color: #21b97a;
        background-color: #70f3bc;
      }
    }

    .house_price {
      color: red;
      span:first-child {
        font-size: 15px;
        margin-right: 3px;
        font-weight: 600;
      }
    }
  }
}
